.homemain{
    display: flex;
    height: 100vh;
    flex-direction:row;
    flex-wrap: wrap-reverse;
    justify-content: center;
}
.home-left{
    flex: 1;
    display: flex;
    margin-left: 20px;
    flex-direction: column;
    justify-content: center;   
    
}

.home-left >h2{
    font-size: xxx-large;
    font-family: 'Montserrat', sans-serif;
}
.home-left >p{
    font-family: 'Montserrat', sans-serif;
}
.home-right{
    display: flex;
    flex: 0.8;
    align-items: center;
    padding: 20px;
    background-color: rgb(244, 253, 246);
}
.home-right>img{
    height: auto;
    width: auto;
}

.buttonlink {
    height: 40px;
    width: 200px;
    border-radius: 20px;
    border:2px solid skyblue;
    background-color: transparent;
    font-size: x-large;
}
.buttonlink >a{
    color:rgb(68, 167, 206);
    text-decoration: none;

}
