
.card{
    
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 400px;
    border-radius: 10px;
    margin: 20px 30px;
    
}
img{
    height:260px;
    width:100%;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;

}

button{
    width:30%;
    margin: 5px;
    height: 30px;
    border-radius: 5px;
    background-color: white;
    border:2px solid skyblue;
    color:skyblue;
}
p{
    margin: 5px;
}